<template>
  <div id="map-container"></div>

  <div id='tip'>{{tip}}</div>
</template>

<style>
  #map-container {
    height: 400px;
    width: 100%;
  }
</style>

<script>
import AMap from 'AMap'
let map
export default {
  data(){
    return {tip:'DEBUG'} 
    //{longitude:0,latitude:0}
  },
  methods:{
    setPosition(pos){
       //
    }
  },
  ready () {
  map = new  AMap.Map('map-container')
    map.plugin('AMap.Geolocation', function () {
      geolocation = new AMap.Geolocation({
          enableHighAccuracy: true,//:true
          timeout: 10000,          //10
          maximumAge: 0,           //00
          convert: true,           //true
          showButton: true,        //true
          buttonPosition: 'LB',    //'LB'
          buttonOffset: new AMap.Pixel(10, 20),//Pixel(10, 20)
          showMarker: true,        //true
          showCircle: true,        //true
          panToLocation: true,     //true
          zoomToAccuracy:true      //false
      })
     map.addControl(geolocation)
     AMap.event.addListener(geolocation, 'complete', (data) => {
        var str=['']
        str.push('' + data.position.getLng())
        str.push('' + data.position.getLat())
        str.push('' + data.accuracy + ' ')
        str.push('' + (data.isConverted ? '' : ''))
        this.tip=str.join('|')
        //document.getElementById('tip').innerHTML = str.join('<br>');
        //	position: absolute;
        console.log(this.tip)
     
    })
 })

  }
}
</script>
<style scoped>
#tip {
	background-color: #fff;
	padding-left: 10px;
	padding-right: 10px;

	font-size: 12px;
	margin-top: 10px;

	border-radius: 3px;
	border: 1px solid #ccc;
	line-height: 30px;
}
</style>


